.main-colors {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 44px;
  }

  &__color {
    margin-top: 85px;
    display: flex;
    justify-content: space-between;

    &:nth-child(2) {
      margin-top: 0;
    }
  }

  &__color-properties {
    margin-top: 16px;
  }

  &__color-property {
    display: grid;
    grid-template-columns: 100px 150px;
    color: #626262;
    font-family: "Open Sans";
    font-size: 14px;
    margin-top: 16px;

    &:first-child {
      margin-top: 0;
    }
  }

  &__property-title {
  }

  &__property-values {
  }

  &__property-value {
    margin-top: 16px;
    color: #393939;

    &:first-child {
      margin-top: 0;
    }
  }

  &__example {
    display: flex;
    align-items: center;
    height: 141px;
    margin-top: 34px;
    margin-left: 6px;
    padding: 10px;
    border: 1px solid #d0d0d0;
    flex-grow: 1;
    box-sizing: border-box;
  }

  &__example-color {
    color: #222222;
    width: 100%;
    height: 100%;
    box-sizing: border-box;

    &_primary-color {
      background-color: #ef6c00;
    }

    &_secondary-color {
      background-color: #283593;
    }

    &_background-color {
      border: 1px solid #d0d0d0;
      background-color: #ffffff;
    }
  }
}