.tooltips {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 33px;
  }

  &_text {
    color: #4c4b4b;
    font-family: "Source Sans Pro";
    font-size: 14px;
    width: 100px;
    cursor: pointer;
  }
}

.MuiTooltip-popper {
  * {
    color: #fefefe !important;
    font-family: "Source Sans Pro" !important;
    font-size: 14px !important;
  }
}

#mui-tooltip-50516 {
  color: green !important;
}

.MuiTooltip-touch {
  width: 1000px !important;
}