.icons {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    //margin-bottom: 44px;
  }

  &__color-name {
    margin-top: 45px;
    margin-bottom: 26px;
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 16px;

    &-second {
      margin-top: 48px;
    }
  }

  &__example_states {
    width: 218px;
    margin-bottom: 31px;

    &_all-icons {
      width: 643px;

      &_second {
        width: 108px;
      }
    }

    &_second {
      width: 208px;
      margin-bottom: 30px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__example_state {
    //flex-grow: 1;

    &-names {
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
    }

    &-name {
      color: #4a4a49;
      font-family: "Open Sans";
      font-size: 14px;
      transform: translateX(10px);
      box-sizing: border-box;

      &.hoverFix {
        transform: translateX(6px);
      }

      &_second {
        transform: translateX(0);
      }
    }

    &-items {
      display: flex;
      //justify-content: space-between;
      align-items: center;
      background-color: #283593;
      //padding: 11px 10px 7px 10px;
      box-sizing: border-box;
      margin-top: 7px;

      &_second {
        background-color: #fff;
        margin-top: 5px;
        margin-left: -12px;
        //padding: 9px 14px 9px 0;

        &-all-icons {
          padding-right: 0;
        }
      }
    }

    &-item {
      margin-right: 37px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}