.fields {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 44px;
  }

  &__inputs {
    display: grid;
    grid-template-columns: 100%;
    column-gap: 11px;
    row-gap: 38px;
  }

  label {
    //color: #000000;
    font-family: "Source Sans Pro";
    font-size: 16px;

    &:not(.Mui-error) {

    }

    &.Mui-focused:not(.Mui-error), &.MuiFormLabel-filled:not(.Mui-error) {
      color: #8d8c8c;
    }
  }

  &__item {

    &-info {
      color: #393939;
      font-family: "Open Sans";
      font-size: 16px;
      margin-bottom: 35px;
    }

    &-input {
      width: 403px;
    }

    &-select {
      width: 403px;
    }
  }

  & .MuiFormHelperText-root {
    color: #8d8c8c;
    font-family: "Source Sans Pro";
    font-size: 12px;
  }
}

.MuiPaper-elevation8 {
  box-shadow: 0 3px 8px rgba(1, 1, 1, 0.24) !important;
}

.MuiMenu-paper {
  //top: 0 !important;
}

.MuiInputBase-input {
  color: #010000 !important;
  font-family: "Source Sans Pro" !important;
  font-size: 16px !important;

  &:focus {
    background-color: #ffffff !important;
  }
}

.MuiList-padding {
  padding: 0 !important;
}

.MuiListItem-button {
  color: #393939 !important;
  font-family: "Source Sans Pro" !important;
  font-size: 16px !important;
  height: 47px;
  min-width: 435px;

  &:hover {
    background-color: rgba(#ef6c00, 0.2) !important;
  }
}

.Mui-selected {
  color: #ef6c00 !important;
  font-size: 16px !important;
  background-color: transparent !important;
}

.Mui-focused:not(.Mui-error) {

  fieldset {
    border: 2px solid #b7b7b7 !important;
  }
}

