.styleguide {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 43px;
}

.container {
  width: 1000px;
}

hr {
  border: 0;
  border-bottom: 1px solid #f2f2f2;
}


.hr {

  &__body-limiter {
    border-bottom: 1px solid #cccccc;
  }

  &_header_font-families {
    margin: 42px 0 93px 0;
  }

  &_font-families_typography {
    margin: 102px 0 93px 0;
  }

  &_typography_main-colors {
    margin: 85px 0 95px 0;
  }

  &_main-colors_links {
    margin: 99px 0 95px 0;
  }

  &_links_menu {
    margin: 146px 0 95px 0;
  }

  &_menu_tooltips {
    margin: 101px 0 95px 0;
  }

  &_tooltips_icons {
    margin: 161px 0 95px 0;
  }

  &_icons_button {
    margin: 89px 0 95px 0;
  }

  &_button_fields {
    margin: 70px 0 95px 0;
  }

  &_fields_footer {
    margin: 96px 0 47px 0;
  }
}

img.layout {
  position: absolute;
  top: 0;
  opacity: 0.2;
  filter: invert(1);
}