.links {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 45px;
  }

  &__list {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    width: 380px;
    height: 50px;
  }

  &__link {
    text-decoration: none;
    transition: 0.2s color ease-in-out;
    cursor: pointer;

    &-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      //align-items: center;
      flex-grow: 1;

      &.bg-for-white-text {
        background-color: #283593;
        flex-grow: 0;
        width: 85px;
        margin-left: 11px;
        padding-right: 10px;
        box-sizing: border-box;
      }
    }
  }

  &__first-link {
    color: #000000;
    font-family: "Source Sans Pro";
    font-size: 16px;

    &:hover {
      color: #ef6c00;
    }
  }

  &__second-link {
    color: #ffffff;
    font-family: "Source Sans Pro";
    font-size: 16px;
    align-self: center;

    &:hover {
      color: #ef6c00;
    }
  }

  &__third-link {
    color: #ef6c00;
    font-family: "Source Sans Pro";
    font-size: 22px;
    font-weight: 700;
    align-self: flex-end;

    &:hover {
      color: #fc831f;
    }
  }
}