.fbIcon {
    cursor: pointer;


    path {
        transition: 0.2s fill ease-in-out;
    }

    &:hover {
        path {
            fill: #ef6c00;
        }
    }

    &_disabled {
        opacity: 0.5;
        cursor: no-drop;
    }

    &:active {
        outline: none !important;
        text-decoration: none !important;
    }
}